<!--  -->
<template>
  <div class="status">
      <mt-cell title="" >
         <div>{{country}}</div>
      </mt-cell>
      <mt-cell title="" >{{name}}</mt-cell>
      <mt-cell title="" >{{sfz_id}}</mt-cell>
       <div style="background-color:white;padding:0.2rem;">
         <p class="p1"></p>
       </div>
       <div class="submit-box-common">
     <mt-button type="primary" size="large" @click="toBack" ></mt-button>
     </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      country:'',
      name:'',
      sfz_id:''
    };
  },
  created(){
   this.country=this.$route.query.country
   this.name=this.$route.query.name
   this.sfz_id=this.$route.query.sfz_id

  },
  components: {},

  computed: {},

  methods: {
    toBack(){
      this.$router.push({path:'/safe'})
    }
  }
}

</script>
<style  lang="less" scoped>
 
</style>


